@import "reset";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

html,
body {
  height: 100%;
}

.wrapper {
  width: 100%;
  color: #fefefe;
  font-family: $font-primary;
  background-color: #060d23;
  min-height: 100vh;
}

.icon {
  fill: #fff;
}

a {
  color: inherit;
}

@import "media";